<template>
    <div class="order">
        <!-- Upload File -->
        <vx-card>
            <div class="flex">
                <div class="w-1/4 mx-2">
                    <!-- <vs-input type="file" @change="importExcel($event)" accept="xls/*" style="width:450px; height:10px" /> -->
                    <input style="width:250px" type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
                </div> 
                <div class="w-1/4 mx-2">
                    <vs-button color="primary" v-on:click="submitFile()">Preview</vs-button>
                </div> 
            </div>
        </vx-card>

        <vx-card class="my-5" v-show="dataUpload.length > 0">
            <div class="tab text-center" >
                <button  v-for="(items,index) in dataUpload" :key="index" :class="tabs == index ? 'active' : 'tablinks'" @click="showContent(index)">{{items}}</button>
            </div>

            <div v-for="(items,index) in dataUploadIsi" :key="index">
                <div v-show="tabs == index" class="tablinks" style="block">
                    <div v-for="(content,index) in dataUploadIsi" :key="index">
                    <div class="table-wrapper mt-5" v-show="tabs == index">
                        <table>
                            <thead>
                                <tr>
                                    <th>Kategori</th>
                                    <th>Produk</th>
                                    <th>Qty</th>
                                    <th>Satuan</th>
                                    <th>Keterangan</th>
                                    <th>Aksi</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,index) in dataUploadIsi[index].isi" :key="index">
                                    <td class="text-center">{{item.kategori}}</td>
                                    <td>{{item.produk}}</td>
                                    <td class="text-center">{{item.qty}}</td>
                                    <td class="text-center">{{item.satuan}}</td>
                                    <td>{{item.keterangan}}</td>
                                    <td class="text-center" >
                                        <vs-button v-if="item.qty > 0" @click="showPopupPT(content.kacab, content.store, content.tglKirim, item.produk, item.qty)"  :color="getQtyItem(item.produk).length > 0 ? 'primary' : 'success'" size="small">Pilih CV / PT</vs-button>
                                        <vs-button v-else disabled color="success" size="small">Pilih CV / PT</vs-button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    </div>
                </div>
            </div>
        </vx-card>

        <!-- Popup Pilih PT -->
        <vs-popup classContent="popup-example" title="Form Pengisian" :active.sync="showOption">
            <!-- <div class="w-full">
                <div class="md:flex md:items-center mb-6 bg-primary text-white p-3">
                        <div class="md:w-1/6 ml-4">
                        <label class="font-bold">#</label>
                        </div>
                        <div class="md:w-1/4 ml-8">
                        <label class="font-bold">Excel</label>
                        </div>
                        <div class="md:w-1/4 text-center ml-10">
                        <label class="font-bold">Form</label>
                        </div>
                    </div>
            </div>
            <div class="w-full">
                    <div class="md:flex md:items-center mb-6 ml-8">
                        <div class="md:w-1/6">
                            <label class="block text-gray-500 font-bold">
                                Toko
                            </label>
                        </div>
                        <div class="md:w-1/4">
                        <label></label>
                        </div>
                        <div class="md:w-2/5">
                        
                        </div>
                    </div>
                    <div class="md:flex md:items-center mb-6 ml-8">
                        <div class="md:w-1/6">
                            <label class="block text-gray-500 font-bold">
                                Kacab
                            </label>
                        </div>
                        <div class="md:w-1/4">
                        <label>{{dataExcel.kacab}}</label>
                        </div>
                        <div class="md:w-2/5">
                        
                        </div>
                    </div>
                    <div class="md:flex md:items-center mb-20 ml-8">
                        <div class="md:w-1/6">
                            <label class="block text-gray-500 font-bold">
                                Tanggal
                            </label>
                        </div>
                        <div class="md:w-1/4">
                        <label>{{dataExcel.tglKirim}}</label>
                        </div>
                        <div class="md:w-2/5">
                        
                        </div>
                    </div>
            </div> -->
            <table class="mb-20">
                <thead>
                    <tr>
                        <th style="height:50px;  width:10px">Toko</th>
                        <td style="width:200px">{{dataExcel.store}}</td>
                        <td style="width:200px"><v-select style="width:250px" data-vv-validate-on="blur" label="nama" :placeholder="'-- Pilih --'" :options="optionStore" v-model="storeSelected"></v-select></td>
                    </tr>
                    <tr>
                        <th style="height:50px;  width:10px">Kacab</th>
                        <td style="width:200px">{{dataExcel.kacab}}</td>
                        <td style="width:200px"><v-select style="width:250px" data-vv-validate-on="blur" label="nama" :placeholder="'-- Pilih --'" :options="optionKacab" v-model="kacabSelected"></v-select></td>
                    </tr>
                     <tr>
                        <th style="height:50px;  width:10px">Tanggal</th>
                        <td style="width:200px">{{dataExcel.tglKirim}}</td>
                        <td style="width:200px"><input type="date" name="dateofbirth" id="dateofbirth" v-model="tanggalSelected"></td>
                    </tr>
                </thead>
            </table>
            <table class="mb-20">
                <thead>
                    <tr>
                        <th style="height:50px; width:10px">Produk</th>
                        <td style="width:200px">{{dataExcel.produk}}</td>
                        <td style="width:200px">
                            <div class="inline-flex space-x-4">
                                <div v-show="product.namaProduct > ''" class="flex-1 mr-2"><span class="d-inline">{{product.namaProduct}} - {{product.namaPT}}</span></div>
                                <div class="flex-1"><vs-button class="d-inline" color="primary" icon="search" size="small" @click="showPopupProduct = true"></vs-button></div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <th style="height:50px;  width:10px">Qty</th>
                        <td style="width:200px">{{dataExcel.qty}}</td>
                        <td style="width:200px" class="text-center ml-6"><vs-input-number class="w-1/3 ml-5" v-model="qty"/></td>
                    </tr>
                </thead>
            </table>
            <div class="vx-row">
                <div class="vx-col w-full text-right">
                    <vs-button color="success" v-if="dataExcel.produk === updateBtn"  class="mr-3 mb-2" @click="updateData(dataExcel.produk)">Update</vs-button>
                    <vs-button color="success"  v-else class="mr-3 mb-2" @click="saveData(dataExcel.produk)">Simpan</vs-button>
                </div>
            </div>
            <!-- Popup Pilih Produk -->
            <vs-popup classContent="popup-example" title="Pilih Produk" :active.sync="showPopupProduct">
                <vs-input class="w-full ml-1 mb-8" icon="search" placeholder="Cari Nama Product" v-model="search"/>
                <table>
                    <tr  v-for="(item, index) in filteredItems" :key="index" >
                        <td style="width:3px" ><vs-radio class="text-center" v-model="product" :vs-value="item"></vs-radio></td>
                        <td style="width:45%">{{item.namaProduct}}</td>
                        <td>{{item.namaPT}}</td>
                    </tr>
                </table>
            </vs-popup>
        </vs-popup>
    </div>
</template>

<script>
export default {
    name: "Order",
    data() {
        return {
            file: '',
            dataUpload: [],
            dataUploadIsi: [],
            dataExcel: {
                kacab: '',
                store:'',
                tglKirim:'',
                produk:'',
                qty:null
            },
            storeSelected: null,
            kacabSelected: null,
            tanggalSelected: null,
            product: {},
            qty:null,
            optionStore:[
                {
                "kode": "IN ('113')",
                "nama": "ABBAD BIN BISYIR"
                },
                {
                "kode": "IN ('124')",
                "nama": "ABBAS BIN ABDUL MUTHOLIB"
                },
                {
                "kode": "IN ('94')",
                "nama": "ABDUL MUTHOLIB"
                },
                {
                "kode": "IN ('181')",
                "nama": "ABDULLAH BIN ABBAS"
                },
                {
                "kode": "IN ('97')",
                "nama": "ABDULLAH BIN MASUD"
                },
                {
                "kode": "IN ('281')",
                "nama": "ABDULLAH BIN SALAM"
                },
                {
                "kode": "IN ('123')",
                "nama": "ABDULLAH BIN UMAR"
                },
                {
                "kode": "IN ('342')",
                "nama": "ABDULLAH BIN UMAR (MEDAN)"
                },
                {
                "kode": "IN ('256')",
                "nama": "ABDULLAH DG SIRUA"
                },
                {
                "kode": "IN ('276')",
                "nama": "ABDULLOH BIN ABDUL ASAD"
                },
                {
                "kode": "IN ('169')",
                "nama": "ABDULLOH BIN ZUBAIR"
                }
            ],
            optionKacab:[
                {
                "kode": "IN ('113')",
                "nama": "SUYONO"
                },
                {
                "kode": "IN ('124')",
                "nama": "BUDI"
                },
                {
                "kode": "IN ('94')",
                "nama": "DODI"
                },
                {
                "kode": "IN ('181')",
                "nama": "DONI"
                },
                {
                "kode": "IN ('97')",
                "nama": "BEDU"
                },
                {
                "kode": "IN ('281')",
                "nama": "JONI"
                }
            ],
            optionProduct:[
                {
                    idPT: 10,
                    namaPT: 'AFAKRUL WIJAYA',
                    idProduct: 13,
                    namaProduct: 'Ayam Reguler / Spicy'
                },
                {
                    idPT: 11,
                    namaPT: 'AYUMINDA BERKAH BERSAMA',
                    idProduct: 13,
                    namaProduct: 'Ayam Reguler / Spicy'
                },
                {
                    idPT: 12,
                    namaPT: 'AFAKRUL WIJAYA',
                    idProduct: 14,
                    namaProduct: 'Ayam Original'
                },
            ],
            tabs: 0,
            showOption: false,
            showPopupProduct: false,
            number: 1,
            selected:[],
            search:'',
            storeData:[],
            pullData :[],
            updateBtn: ''
        }
    },

    computed: {
        selectAll: {
            get () {
                return this.selected.length === this.optionProduct.length
            },
            set (value) {
                this.selected = []

                if (value) {
                this.optionProduct.forEach((select) => {
                    this.selected.push(select)
                })
                }
            }
        },

        filteredItems(){
            return this.optionProduct.filter(item => {
                if(item.namaProduct == undefined)
                return false
                return item.namaProduct.toLowerCase().indexOf(this.search.toLowerCase()) > -1
            })
        }
    },

    mounted() {
        if (localStorage.getItem('storeData')) {
            try {
                this.pullData = JSON.parse(localStorage.getItem('storeData'))
            } catch (e) {
                localStorage.removeItem('storeData')
            }
        }
    },

    methods: {
        getQtyItem (produk) {
            let data = this.pullData.filter(item => item.name === produk)
            return data
        },
    
        submitFile(){
            this.$vs.loading()
            let formData = new FormData();
            formData.append('file', this.file);
            this.$http.post( 'http://117.53.47.96:1408/upload',formData)
            .then(response => {
                if(response.data.status === "SUKSES"){
                    this.dataUpload = response.data.message.sheet
                    this.dataUploadIsi = response.data.message.data
                }
            })
            .catch(error => {
                this.$swal.fire(
                    'Oopss !',
                     error,
                    'error'
                )
            })
            .finally(() =>{this.$vs.loading.close()})
        },

        handleFileUpload(){
            this.file = this.$refs.file.files[0];
        },
        showContent(sheet){
            this.tabs = sheet
        },

        showPopupPT(kacab,store,tglKirim,produk,qty) {
            if (localStorage.getItem('storeData') === null || localStorage.getItem('storeData') === 0) {
                    this.showOption = true
                    this.dataExcel.kacab = kacab,
                    this.dataExcel.store = store,
                    this.dataExcel.tglKirim = tglKirim,
                    this.dataExcel.produk = produk,
                    this.dataExcel.qty = qty
                    this.qty = qty
            } else {
                const idx = this.pullData.findIndex(item => item.name === produk)
                if (idx === -1) {
                    this.showOption = true
                    this.storeSelected = null
                    this.kacabSelected = null
                    this.tanggalSelected = null
                    this.dataExcel.kacab = kacab,
                    this.dataExcel.store = store,
                    this.dataExcel.tglKirim = tglKirim,
                    this.dataExcel.produk = produk,
                    this.dataExcel.qty = qty
                    this.product = {}
                    this.qty = qty
                } else {
                    let data = this.pullData.filter(item => item.name === produk)
                    this.showOption = true
                    this.dataExcel.kacab = kacab,
                    this.dataExcel.store = store,
                    this.dataExcel.tglKirim = tglKirim,
                    this.dataExcel.produk = produk,
                    this.dataExcel.qty = qty,
                    this.storeSelected = data[0].store,
                    this.kacabSelected = data[0].kacab,
                    this.tanggalSelected = data[0].tanggal,
                    this.product = data[0].product,
                    this.qty = data[0].qty,
                    this.updateBtn = data[0].name
                }
            }
            
        },

        updateData(produk) {
            const idx = this.pullData.findIndex(item => item.name === produk)
            if (idx === -1) {
                this.showOption = true
                this.storeSelected = null
                this.kacabSelected = null
                this.tanggalSelected = null
                this.product = {}
                this.showOption = false
            } else {
                for (var item in this.pullData) {
                    if (this.pullData[item].name === produk) {
                        this.pullData[item].name = produk,
                        this.pullData[item].store = this.storeSelected,
                        this.pullData[item].tanggal = this.tanggalSelected,
                        this.pullData[item].product = this.product,
                        this.pullData[item].qty = this.qty,
                        
                        localStorage.setItem('storeData', JSON.stringify(this.pullData))
                        this.showOption = false
                        return
                    }
                }
            }
        },

        saveData(nama) {
            const tempData = {
                name: nama,
                store: this.storeSelected,
                kacab: this.kacabSelected,
                tanggal: this.tanggalSelected,
                product: this.product,
                qty: this.qty,
            }

            this.storeData.push(tempData);
         
            localStorage.setItem("storeData", JSON.stringify(this.storeData))
            this.showOption = false
            this.pullData =  JSON.parse(localStorage.getItem('storeData'))
        }
    }

}
</script>

<style>
.table-wrapper{
  overflow-y: scroll;
  height: 560px;
}

.table-wrapper th{
    position: sticky;
    top: 0;
    z-index: 1;
}

table{
  border-collapse: collapse;
  width: 100%;
}

th{
    font-size: 12px;
    background: #7367f0;
    color: #fff;
    border: solid 1px #DDEFEF;
    padding: 5px;
    text-align: center;
    white-space: nowrap;
}

table th.sorted.asc::after {
	display: inline-block;
	content: ' ▼';
}

 table th.sorted.desc::after {
	display: inline-block;
	content: ' ▲';
}

td,th{
  padding: 5px;
  white-space: nowrap;
  border: solid 1px #DDEFEF;
}

/* Style the tab */
.tab {
  overflow: hidden;
}

/* Style the buttons inside the tab */
.tab button {
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
  background-color: #999;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: #fff;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #aaaaaa;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #7367f0;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border-top: none;
}

[type="date"] {
  background:#fff url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png)  97% 50% no-repeat ;
}
[type="date"]::-webkit-inner-spin-button {
  display: none;
}
[type="date"]::-webkit-calendar-picker-indicator {
  opacity: 0;
}

/* custom styles */
label {
  display: block;
}
input {
  border: 1px solid #c4c4c4;
  border-radius: 4px;
  background-color: #fff;
  padding: 8px 5px;
  width: 250px;
}
</style>

<style lang="stylus">
.popup-example
  .vs-input
    float left
    width 50%
    margin 10px
    margin-top 5px
  .con-select
    margin-left 10px
    width 50%
    margin-bottom 10px
</style>